.description {
  word-break: break-all;
  display: block;
  width: 400px;
  line-height: 1.2;
  transition: color 0.4s;
  font-weight: 400;
  color: #4d4d4d;
}

.completed {
  word-break: break-all;
  display: block;
  line-height: 1.2;
  transition: color 0.4s;
  font-weight: 400;
  color: #cdcdcd;
  text-decoration: line-through;
}

.created {
  font-size: 13px;
  color: gray;
}

.icon {
  position: absolute;
  top: 0;
  bottom: -3px;
  width: 30px;
  height: 40px;
  font-size: 34px;
  color: #cc9a9a;
  margin: auto 0 11px;
  transition: color 0.2s ease-out;
  cursor: pointer;
}

.icon:hover {
  color: #af5b5e;
}

.icon-destroy {
  right: 10px;
}

.icon-destroy:after {
  content: '×';
}

.icon-edit {
  font-size: 19px;
  right: 45px;
}

.icon-edit:after {
  content: '✎';
}

.new-todo,
.editInput {
  position: relative;
  margin: 0;
  width: 100%;
  font-size: 24px;
  font-family: inherit;
  font-weight: inherit;
  line-height: 1.4em;
  color: inherit;
  padding: 6px;
  border: 1px solid #999;
  box-shadow: inset 0 -1px 5px 0 rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.editing {
  border-bottom: none;
  padding: 0;
}
.edit {
  /* .todo-list li .edit { */
  display: none;
  width: calc(100% - 43px);
  padding: 12px 16px;
  margin: 0 0 0 43px;
}

.toggle {
  text-align: center;
  width: 40px;
  /* auto, since non-WebKit browsers doesn't support input styling */
  height: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  border: none; /* Mobile Safari */
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
}

.toggle {
  opacity: 0;
}
.icon .icon-pause,s
.icon-play {
  font-size: 18px;
  width: 18px;
  height: 18px;
}

.icon-play {
  font-size: 15px;
}

